import { newSpecPage } from "@stencil/core/testing";
import { RwcFoldingSquares } from "../folding-squares";

describe("RwcFoldingSquares", () => {
  it("should render rwc-folding-squares component", async () => {
    const page = await newSpecPage({
      components: [RwcFoldingSquares],
      html: `<rwc-folding-squares></rwc-folding-squares>`,
    });

    expect(page.root).toEqualHtml(`
      <rwc-folding-squares>
        <mock:shadow-root>
          <div class="folding-cube">
            <div class="cube1 cube"></div>
            <div class="cube2 cube"></div>
            <div class="cube4 cube"></div>
            <div class="cube3 cube"></div>
          </div>
        </mock:shadow-root>
      </rwc-folding-squares>
    `);
  });
});
